import React from "react"
import { Link } from "gatsby"
import { StaticImage } from "gatsby-plugin-image"
import Layout from "../../components/layout"
import Seo from "../../components/seo"
import "../../styles/main-article-case-study.css"

export default function Home() {
  const description = `神奈川のIT相談ならホットソウルにお任せください！当社がデジタル化やIT活用でサポートさせていただいた実績をご紹介します。 | 2022年8月 有限会社安室設備様 「業務用PCセットアップサポート」`
  const pageUrl = `https://www.hotsoul.jp/case-study/ryugu-komiya`
  const imageUrl = `https://www.hotsoul.jp/ogp_ryugu-komiya.jpg`

  return (
    <Layout>
      <Seo title="サポート実績" description={description} imageUrl={imageUrl} pageUrl={pageUrl} />
      <div className="wrap">

        <section className="mv_ac-cs">
          <div className="mv_ac-cs-inner">
            <p className="txt1"><span>サポート実績</span></p>
            <p className="txt2">当社がデジタル化や<br className="sp"/>IT活用で<br className="pc"/>サポートさせていただいた<br className="sp"/>実績をご紹介します。
            </p>
          </div>
        </section>

        <section className="ac-cs">
          <h2>業務改善・生産性向上</h2>
          <div className="ac-cs-inner">
            <div className="flex">
              <div className="flex-item left">
                <StaticImage
                  src="../../images/main-case-study/ryugu-komiya/ph1.jpg"
                  alt="合同会社竜宮小宮 様"
                  placeholder="blurred"
                />
              </div>
              <div className="flex-item right">
                <p className="date">2025.4.10</p>
                <p className="title"><span className="bg-yellow">合同会社竜宮小宮 様<br/></span></p>
              </div>
            </div>
            <div className="flex-table-box">
              <div className="flex-table first">
                <div className="flex-table-item left">
                  <p>お悩み</p>
                </div>
                <div className="flex-table-item right">
                  <ul>
                    <li>経理業務の作業が面倒なので改善を検討したい</li>
                    <li>現状は何とかこなせているが、今後の業容拡大を考えると不安がある</li>
                    <li>第三者の目線で効率化できる点がないかチェックして欲しい</li>
                  </ul>
                </div>
              </div>
              <div className="flex-table">
                <div className="flex-table-item left">
                  <p>サポート</p>
                </div>
                <div className="flex-table-item right">
                 <ul>
                   <li>業務内容をヒアリングしてフロー図を作成</li>
                   <li>フロー図を共有しながら現状を俯瞰</li>
                   <li>一緒にムリ／ムダ／ムラを探しながら、改善策をご提案</li>
                 </ul>
                </div>
              </div>
              <div className="flex-table">
                <div className="flex-table-item left">
                  <p>結果</p>
                </div>
                <div className="flex-table-item right">
                  <ul>
                    <li>大きなムダはないことが客観的に評価され安心できた</li>
                    <li>真の課題は「人には任せられない」という思いこみであることを発見</li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
          <div className="bg-orange ac-cs-cont">
            <div className="ac-cs-inner">
              <div className="item-box">
                <p className="ttl-support">サポートの背景</p>
                <div className="item first">
                  <div className="flex-support q">
                    <div className="flex-support-item left"></div>
                    <div className="flex-support-item right">まずは自己紹介と御社のご説明をお願いします。</div>
                  </div>
                  <div className="flex-support a">
                    <div className="flex-support-item left">小宮さま：</div>
                    <div className="flex-support-item right">弊社は、横浜市磯子区で介護事業と広告制作を父と二人で営む、小さな会社です。私は映像制作会社やデザイン会社で23年間サラリーマンとして働いた後、ケアマネージャーとして既に起業していた父の会社に、3年前に加わりました。現在は、父がケアマネージャーとして介護事業を担当し、私はホームページ制作や映像制作を通じて、地域の皆さまのお役に立てるよう活動しています。</div>
                  </div>
                </div>
                <div className="item">
                  <div className="flex-support q">
                    <div className="flex-support-item left"></div>
                    <div className="flex-support-item right">どのような事情で外部のサポートが必要と思われましたか？</div>
                  </div>
                  <div className="flex-support a">
                    <div className="flex-support-item left">小宮さま：</div>
                    <div className="flex-support-item right">クライアントへの請求書発行と入金確認、協力会社からの請求書受領と支払などの経理業務が毎月あり、それに煩雑さを感じてはいたのですが、何が正解なのかも分からなかったので、改善したくてもしようがないというモヤモヤした状況が続いていました。</div>
                  </div>
                </div>
                <div className="item">
                  <div className="flex-support q">
                    <div className="flex-support-item left"></div>
                    <div className="flex-support-item right">弊社にご相談いただいたきっかけは？</div>
                  </div>
                  <div className="flex-support a">
                    <div className="flex-support-item left">小宮さま：</div>
                    <div className="flex-support-item right">若杉さんとは定期的にお互いのビジネスについて情報交換をしているのですが、その中で業務改善や生産性向上のコンサルティングもやっていますよというお話があったので、一度相談してみることにしました。</div>
                  </div>
                </div>
              </div>
              <StaticImage
                src="../../images/main-case-study/ryugu-komiya/ph2.jpg"
                alt="サポートの背景"
                placeholder="blurred"
              />
            </div>
          </div>
          <div className="ac-cs-cont">
            <div className="ac-cs-inner">
              <div className="item-box">
                <p className="ttl-support">相談内容とサポート</p>
                <div className="item first">
                  <div className="flex-support q">
                    <div className="flex-support-item left"></div>
                    <div className="flex-support-item right">ご相談内容は？</div>
                  </div>
                  <div className="flex-support a">
                    <div className="flex-support-item left">小宮さま：</div>
                    <div className="flex-support-item right">経理業務（支払いや請求）について、今のままで良いのか、もっと改善できるところがあるのか、客観的な意見を聞いてみたいと思っていました。</div>
                  </div>
                </div>
                <div className="item">
                  <div className="flex-support q">
                    <div className="flex-support-item left"></div>
                    <div className="flex-support-item right">特にお困りの点は？</div>
                  </div>
                  <div className="flex-support a">
                    <div className="flex-support-item left">小宮さま：</div>
                    <div className="flex-support-item right">サラリーマン時代は、請求書などの処理はシステムに入力してプリントアウトし、クリアファイルに入れておけば、経理担当者がすべて対応してくれていました。そのため、独立後は帳票の具体的な扱い方が分からず、不安を感じていました。また、他の小さな会社では帳票処理をどのように行っているのかも気になっていました。一方で、このままでは会社の成長にともなう業務量の増加に対応できないのではという不安もあり、そのはざまで悩む日々が続いていました。</div>
                  </div>
                </div>
              </div>
              <StaticImage
                src="../../images/main-case-study/ryugu-komiya/ph3.jpg"
                alt="相談内容とサポート"
                placeholder="blurred"
              />
            </div>
          </div>
          <div className="bg-orange ac-cs-cont">
            <div className="ac-cs-inner">
              <div className="item-box">
                <p className="ttl-support">今後の展望</p>
                <div className="item first">
                  <div className="flex-support q">
                    <div className="flex-support-item left"></div>
                    <div className="flex-support-item right">今回、弊社のサポートを受けてみて良かった点をお聞かせください。</div>
                  </div>
                  <div className="flex-support a">
                    <div className="flex-support-item left">小宮さま：</div>
                    <div className="flex-support-item right">目の前にある課題は、『支払いや請求が面倒。でも自分にしかできないからどうしよう』でしたが、根本的な課題としては『（現状のやり方のままでは）支払いや請求を手放して誰かにお願いすることができない』ことだとわかり、目から鱗が落ちるような体験でした。今後は経理処理を他の人に任せることで、自分は事業の拡大に専念できるという安心感を持つことができました。</div>
                  </div>
                </div>
                <div className="item">
                  <div className="flex-support q">
                    <div className="flex-support-item left"></div>
                    <div className="flex-support-item right">逆に、改善点や期待することがあれば。</div>
                  </div>
                  <div className="flex-support a">
                    <div className="flex-support-item left">小宮さま：</div>
                    <div className="flex-support-item right">特にありません。強いて挙げるとすれば、おすすめの経理ソフトやシステムなどを教えていただけると嬉しいです。もっとも、そのあたりは税理士さんの領域かもしれませんね。</div>
                  </div>
                </div>
                <div className="item">
                  <div className="flex-support q">
                    <div className="flex-support-item left"></div>
                    <div className="flex-support-item right">竜宮小宮様の今後の展望をお聞かせください。</div>
                  </div>
                  <div className="flex-support a">
                    <div className="flex-support-item left">小宮さま：</div>
                    <div className="flex-support-item right">
                      弊社の理念は、「皆が活き活きとした、自由でやさしい世界を創る」ことです。
                      その実現に向けて、広告事業部ではWebマーケティングを通じ、まじめに頑張っているけれど自己アピールや商売が少し苦手な中小企業の皆さまを応援したいと考えています。
                      今後ともどうぞよろしくお願いいたします。
                    </div>
                  </div>
                </div>
              </div>
              <StaticImage
                src="../../images/main-case-study/ryugu-komiya/ph4.jpg"
                alt="今後の展望"
                placeholder="blurred"
              />
			        <div className="flex-share">
                <ul className="sns">
                  <li>
                    <a href="https://twitter.com/share?url=https://www.hotsoul.jp/case-study/ryugu-komiya" rel="nofollow noopener noreferrer" target="_blank">
                      <StaticImage
                        src="../../images/icon-tw.png"
                        alt="twitterシェア"
                        placeholder="blurred"
                      />
                    </a>
                  </li> 
                  <li>
                    <a href="http://www.facebook.com/share.php?u=https://www.hotsoul.jp/case-study/ryugu-komiya" rel="nofollow noopener noreferrer" target="_blank">
                      <StaticImage
                        src="../../images/icon-fb.png"
                        alt="facebookシェア"
                        placeholder="blurred"
                      />
                    </a>
                  </li>
                  <li>
                    <a href="https://social-plugins.line.me/lineit/share?url=https://www.hotsoul.jp/case-study/ryugu-komiya" rel="nofollow noopener noreferrer" target="_blank">
                      <StaticImage
                        src="../../images/icon-line.png"
                        alt="LIMEシェア"
                        placeholder="blurred"
                      />
                    </a>
                  </li>
                </ul>
			        </div>
              <br/>                
              <p className="archive"><Link to={`/case-study/articles`}>他の記事も読む</Link></p>
            </div>
          </div>
        </section>
      </div>
    </Layout>
  )
}